import { useEffect, useRef, useState } from "react";
import { DownloadingLoopIcon } from "@wptb/ui/icons/loaders";
import store from "@wptb/external/store";
import { setTable } from "@wptb/external/store/table";
import { parseTable } from "@wptb/external/utils";
import { createRoot } from "@wptb/external/react-dom/client";
import { StrictMode } from "@wptb/external/react";
import { Provider } from "@wptb/external/react-redux";
import { Table } from "@wptb/external/components";

interface TableProps {
  id: string;
  shouldLoad: boolean;
  setShouldLoad: (shouldLoad: boolean) => void;
}

const TablePage = ({ id, shouldLoad, setShouldLoad }: TableProps) => {
  const elRef = useRef<HTMLDivElement>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    if (!shouldLoad) {
      setIsLoading(false);
      return;
    }
    setIsLoading(true);
    const abortCtrl = new AbortController();

    const url = WPTB_CFG.API_BASE + "/v1/table?id=" + id;
    fetch(url, {
      signal: abortCtrl.signal,
    })
      .then((response) => response.json())
      .then((data) => {
        const table = parseTable(data.table)!;
        table.title = data.name;
        table.id = id;
        store.dispatch(
          setTable({
            id,
            table,
          })
        );
        setIsLoading(false);
        setShouldLoad(false);
      }).catch((_) => undefined);

    return () => {
      abortCtrl.abort();
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
}, [id]);

useEffect(() => {
  if (!elRef.current || isLoading) {
    return;
    }
    const root = createRoot(elRef.current);
    root.render(
      <StrictMode>
        <Provider store={store}>
          <Table id={id} />
        </Provider>
      </StrictMode>
    );
    return () => {
      root.unmount();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [elRef.current, isLoading, id]);

  return isLoading ? (
    <div className="wptb-table-loading-icon">
      <DownloadingLoopIcon />
    </div>
  ) : (
    <div className="wptb-root-app" ref={elRef} />
  );
};

export default TablePage;
