import { BlockEditProps, registerBlockType } from "@wordpress/blocks";
import metadata from "./block.json";
import {
  BlockControls,
  InspectorControls,
  useBlockProps,
} from "@wordpress/block-editor";
import { useEffect, useState } from "react";
import BuilderPage from "./components/builder";
import TablePage from "./components/table";
import PickerPage from "./components/picker";
import { Button, PanelBody, ToolbarButton } from "@wordpress/components";
import { isNumeric } from "@wptb/utils/check";
import store from "@wptb/external/store";
import { changeId } from "@wptb/external/store/table";

interface WPTBBlockProps {
  id?: string;
}

const Edit = ({
  attributes,
  setAttributes,
}: BlockEditProps<WPTBBlockProps>) => {
  const blockProps = useBlockProps({
    className: "wptb-gutenberg",
  });
  const [shouldLoad, setShouldLoad] = useState(isNumeric(attributes.id));
  const [builder, setBuilder] = useState(false);
  const [tblId, setTblId] = useState(attributes.id);

  useEffect(() => {
    setTblId(attributes.id);
  }, [attributes.id]);

  const setTable = (id: string, isNew?: boolean) => {
    if (id === attributes.id) {
      setTblId(id);
      return;
    }
    if (isNew) {
      setBuilder(true);
      setTblId(id);
      setShouldLoad(false);
    } else {
      setShouldLoad(true);
      setAttributes({ id });
    }
  };

  const onSave = ([action, id]: [string, number]) => {
    if (action === "saved") {
      store.dispatch(changeId({ oldId: tblId!, newId: id.toString() }));
      setTblId(id.toString());
      setAttributes({ id: id.toString() });
    }
  };

  return (
    <div {...blockProps}>
      {tblId ? (
        <TablePage
          id={tblId}
          shouldLoad={shouldLoad}
          setShouldLoad={setShouldLoad}
        />
      ) : (
        <PickerPage setTable={setTable} id={attributes.id} />
      )}
      {builder && tblId && (
        <BuilderPage
          onClose={() => setBuilder(false)}
          onSave={onSave}
          id={tblId}
        />
      )}
      <InspectorControls>
        <PanelBody title="WP Table Builder" initialOpen>
          <Button
            variant="secondary"
            onClick={() => setTblId("")}
            icon="remove"
            className="wptb-show-picker-btn"
          >
            Change Table
          </Button>

          <Button
            variant="primary"
            onClick={() => setBuilder(true)}
            icon="share-alt2"
            className="wptb-open-editor-btn"
          >
            Open in Editor
          </Button>
        </PanelBody>
      </InspectorControls>
      <BlockControls>
        <ToolbarButton
          icon="share-alt2"
          label="Open in Editor"
          onClick={() => setBuilder(true)}
          onPointerEnterCapture={null}
          onPointerLeaveCapture={null}
          placeholder="Open in Editor"
        />
      </BlockControls>
    </div>
  );
};

const save = () => {
  return null;
};

// @ts-expect-error typical gutenberg
registerBlockType(metadata.name, {
  title: metadata.title,
  category: metadata.category,
  attributes: metadata.attributes,
  edit: Edit,
  save,
  transforms: {
    from: [
      {
        type: "shortcode",
        tag: "wptb",
        attributes: {
          id: {
            type: "number",
            shortcode: ({ named }: any) => {
              return parseInt(named.id, 10);
            },
          },
        },
        priority: 1,
      },
    ],
  },
});
